	module.exports = {"data":{"post":{"id":"/home/travis/build/h0ssimo/beta/content/posts/portfolio/2018-05-04--The-Prof/index.md absPath of file >>> MarkdownRemark","html":"<h2>The Professor</h2>\n<p>This is the third character in my series of character sculpts for my personal story. He is simple, perfectionist, scientist and also dives deep into the relationship between species and actively develops theories on that matter.</p>\n<img src=\"/images/characters/professor/professor_by_hossimo_front.jpg\" alt=\"professor_by_hossimo_front\" class=\"responsive\">\n<h3>Background</h3>\n<p>He lives in the year 2279, and works in a secret lab in a prison in the US trying to develop ways to enhance human DNA.</p>\n<p>He has invented a device for his glasses, that projects all he wants to see in a holographic manner. And has only two buttons to use when necessary. (You can see the details in the close-up shots.) In fact, it’s the only thing he’s obsessed about in a world overwhelming by tech.</p>\n<img src=\"/images/characters/professor/professor_head_by_hossimo_front.jpg\" alt=\"professor_head_by_hossimo_front\" class=\"responsive\">\n<p>He also personally studies on a project in which he tries to confirm his theory for teleportation to the parallel worlds. During his studies, he finds out that the teleportation is only availabe once in a half-million years in Antarctica, and stays open for some time. And the time is very close to that event.</p>\n<img src=\"/images/characters/professor/professor_by_hossimo_left.jpg\" alt=\"professor_by_hossimo_left\" class=\"responsive\">\n<p>He finds that breakthrough in space, and realises that there is a world in which all the mythical creatures he studied about in elder books, lived there. Letting the US goverment know, he tries to seize the opportunity to grab some new DNAs to use their features in humans.</p>\n<p>Things will not go as planned, and other species like Orcs, Argonians, Vampires, Divs, Demons, Ogres etc. find their way to the earth to find water.</p>\n<p>The Professor decides to lead a group to make everyone understand that the war is not the only way.</p>\n<img src=\"/images/characters/professor/professor_by_hossimo_right.jpg\" alt=\"professor_by_hossimo_right\" class=\"responsive\">\n<h3>Sculpting Process</h3>\n<p>I used Maya for creating the base meshes, Mari to texture the face, ZBrush to sculpt details, and Photoshop to compose the renderpasses I got from ZBrush BPR. Also all the clothes were done manually.</p>\n<img src=\"/images/characters/professor/professor_head_by_hossimo_right.jpg\" alt=\"professor_head_by_hossimo_right\" class=\"responsive\">  \n  \n<img src=\"/images/characters/professor/professor_by_hossimo_all.jpg\" alt=\"professor_by_hossimo_all\" class=\"responsive\">\n<img src=\"/images/characters/professor/professor_head_by_hossimo_all.jpg\" alt=\"professor_head_by_hossimo_all\" class=\"responsive\">\n<p>For higher resolution images, please visit my Artstation.</p>","fields":{"slug":"/The-Prof/","prefix":"portfolio/2018-05-04"},"frontmatter":{"title":"The Professor","author":"Hossein Moayed","category":"portfolio","date":"2018-05-04T00:00:00.000Z","cover":{"childImageSharp":{"resize":{"src":"/static/professor_by_hossimo_left-5c1c1d4f9a85cc356615d4e5fe01ebcc-ada8c.jpg"}}}}},"authornote":{"id":"/home/travis/build/h0ssimo/beta/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p>To make it short, I’m a 3D Character Artist experienced in character pipelines for both animations and games.</p>"},"site":{"siteMetadata":{"facebook":{"appId":"298710090861252"}}}},"pathContext":{"slug":"/The-Prof/","prev":{"id":"/home/travis/build/h0ssimo/beta/content/posts/portfolio/2017-10-27--The-Argonian/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/The-Argonian/","prefix":"portfolio/2017-10-27"},"frontmatter":{"title":"The Throat Cutter","category":"portfolio","tags":["fantasy","sculpture"],"date":"2017-10-27T00:00:00.000Z"}},"next":{"id":"/home/travis/build/h0ssimo/beta/content/posts/portfolio/2018-06-11--Minotaur/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/Minotaur/","prefix":"portfolio/2018-06-11"},"frontmatter":{"title":"The Minotaur","category":"portfolio","tags":["fantasy","sculpture"],"date":"2018-06-11T00:00:00.000Z"}}}}

